.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;

}

.custom-loader {
    width:100px;
    height:100px;
    margin-right: 50px;
    border-radius:50%;
    background:conic-gradient(#0000 10%,#078ff2);
    -webkit-mask:radial-gradient(farthest-side,#0000 calc(100% - 16px),#000 0);
    animation:s3 2s infinite linear;
  }
  @keyframes s3 {to{transform: rotate(1turn)}}