.navigation {
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 80px;
  width: 100%;
  background-color: #333;
  z-index: 10;

  a {
    text-decoration: none;
  }

  &_bars {
    display: none;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    display: none;
    color: white;
    cursor: pointer;

    span {
      display: block;
      width: 35px;
      height: 5px;
      background-color: white;
      border-radius: 8px;

      &::after,
      &::before {
        content: "";
        display: block;
        width: 35px;
        height: 5px;
        background-color: white;
        border-radius: 8px;
        transform: translateY(5px);
      }

      &::before {
        transform: translateY(-10px);
      }
    }
  }

  &_logo {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 35px;
    color: white;

    &_img {
      height: 45px;
      margin-right: 15px;
    }
  }

  &_item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    height: 100%;

    &_login {
      height: 40px !important;
      border-radius: 8px;
      background-color: #007bff;
      border: none;
      cursor: pointer;
    }

    &_basket {
      position: relative;
      cursor: pointer;
    }

    &_user::after {
      content: "";
    }

    a {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 20px;
      border-bottom: 2px solid transparent;
      transition: border-bottom 0.4s;
      height: 100%;
      color: white;

      &:hover {
        border-bottom: 2px solid #d5d5d5;
      }
      &:hover:nth-child(5), &:hover:nth-child(4) {
        border-bottom: 2px solid transparent;
      }
    }

    span {
      position: absolute;
      top: 0;
      right: 0;
      display: block;
      text-align: center;
      line-height: 25px;
      background-color: red;
      color: white;
      width: 25px;
      height: 25px;
      border-radius: 50px;
    }
  }
}

@media screen and (max-width: 768px) {
  .navigation {
    justify-content: space-between;
    padding: 0 20px;
    &_item {
      position: fixed;
      top: 0;
      right: 0;
      flex-direction: column;
      background: #222;
      width: 100vw;
      height: 100vh;
      transform: translatex(100vw);
      transition: 1s;

      a {
        height: 100px;
      }

      &_basket {
        span {
          top: 10px;
          right: 10px;
        }
      }

      li {
        padding: 20px;
        a {
          font-size: 25px;
          color: rgb(230, 230, 230);
        }

        &:hover {
          text-decoration: underline;
        }
      }

      &_active {
        transform: translatex(0);
      }
    }

    &_bars {
      display: flex;
      z-index: 1000;
    }
  }
}
